import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom';
import {
    Col,
    Container,
    Row,
    Image,
    ListGroup,
    Card
} from 'react-bootstrap';
import './activitieStyles.css';

import Loader from '../../components/Loader/Loader';
import Message from '../../components/Message/Message';
import { listActivitieProductsDetails } from '../../actions/activitieProductAction.js'
import Meta from '../../components/Helmet/Meta';

const ActivitieProductDetailScreen = ({ history, match }) => {

    const dispatch = useDispatch()

    const activitieProductDetails = useSelector(state => state.activitieProductDetails)
    const { loading, error, activitieProduct } = activitieProductDetails

    useEffect(() => {
        dispatch(listActivitieProductsDetails(match.params.id))
    }, [dispatch, match])

  

    return (
        <div className="productScreen">
            <Meta
                title=" | Activitie Product"
            />
            <Container>
                <Link className="btn btn-go-back btn-dark" to="/activitie">GO BACK</Link>
                {
                    loading ?
                        <Loader />
                        : error
                            ? <Message variant='danger'>{error}</Message>
                            : (
                                <Row className="p-3 service2-product" >
                                    <Col md={6}>
                                        <Image className="mx-auto image-service2"
                                            src={activitieProduct.image}
                                            alt={activitieProduct.name}
                                            width={500}
                                        />
                                    </Col>
                                    <Col md={3}>
                                        <ListGroup className="borderless" variant='flush'>
                                            <ListGroup.Item>
                                                <h2>{activitieProduct.name}</h2>
                                            </ListGroup.Item>
                                            <ListGroup.Item>
                                                <h4> note : {activitieProduct.note}</h4>
                                            </ListGroup.Item>
                                     
                                        </ListGroup>
                                    </Col>
                                    <Col md={3}>
                                        <Card>
                                            <ListGroup variant='flush'>
                                           
                                              
                                            </ListGroup>
                                        </Card>
                                    </Col>
                                </Row>
                            )
                }
            </Container>
        </div>
    )
}


export default ActivitieProductDetailScreen
