import React, { useState, useEffect } from 'react'
import axios from 'axios'
import {
    Form,
    Button,
    Container,
    Row,
    Col
} from 'react-bootstrap'
import { Link, useHistory } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../../../components/Message/Message'
import Loader from '../../../components/Loader/Loader'
import FormContainer from '../../../components/FormContainer/FormContainer'
import { listService1ProductsDetails, updateService1 } from '../../../actions/productServices1Actions'
import { SERV1_UPDATE_RESET } from '../../../constants/productConstants'
import Meta from '../../../components/Helmet/Meta'

const Service1ListEdit = ({ match }) => {

    const [name, setName] = useState('')
    const [image, setImage] = useState('')
    const [description, setDescription] = useState('')
    const [price, setPrice] = useState('')
    const [target_plant, setTarget_plant] = useState('')
    const [quantity, setQuantity] = useState('')
    const [serv1_power, setServ1_power] = useState('')
    const [uploading, setUploading] = useState(false)

    const productId = match.params.id

    const dispatch = useDispatch()
    let history = useHistory()

    const productServices1Details = useSelector(state => state.productServices1Details)
    const { loading, productServices1, error } = productServices1Details

    const Services1Update = useSelector(state => state.Services1Update)
    const { loading: loadingUpdate, success: successUpdate, error: errorUpdate } = Services1Update

    useEffect(() => {
        if (successUpdate) {
            dispatch({ type: SERV1_UPDATE_RESET })
            history.push('/admin/productlist')
        } else {
            if (!productServices1.name || productServices1._id !== productId) {
                dispatch(listService1ProductsDetails(productId))
            } else {
                setName(productServices1.name)
                setDescription(productServices1.description)
                setPrice(productServices1.price)
                setImage(productServices1.image)
                setTarget_plant(productServices1.target_plant)
                setQuantity(productServices1.quantity)
                setServ1_power(productServices1.serv1_power)
            }
        }
    }, [history, productServices1, dispatch, productId, successUpdate])

    const submitHandler = (e) => {
        e.preventDefault()
        dispatch(updateService1({
            _id: productId,
            name,
            image,
            price,
            description,
            target_plant,
            quantity,
            serv1_power
        }))
    }

    const uploadFileHandler = async (e) => {
        const file = e.target.files[0]
        const formData = new FormData()
        formData.append('image', file)
        setUploading(true)

        try {
            const config = {
                headers: {
                    'Content-type': 'multipart/form-data'
                }
            }

            const { data } = await axios.post('/api/upload', formData, config)

            setImage(data)
            setUploading(false)

        } catch (error) {
            console.error(error)
            setUploading(false)
        }
    }

    return (
        <Container style={{ marginBottom: '50px' }}>
            <Meta
                title=" | Admin Serv1 Edit"
            />
            <FormContainer>
                <h2 style={{ marginTop: '120px', textAlign: 'center' }}> Serv 1 Profile</h2>
                <Link to='/admin/productlist' className='btn btn-light my-3'>
                    GO BACK
                </Link>
                {loading && <Loader />}
                {error && <Message variant='danger'>{error}</Message>}
                {loadingUpdate && <Loader />}
                {errorUpdate && <Message variant='danger'>{errorUpdate}</Message>}
                {successUpdate && <Message variant='success'>Profile Updated!</Message>}
                <Form onSubmit={submitHandler}>
                    <Row>
                        <Col md={6}>
                            <Form.Group controlId='name'>
                                <Form.Label>Name</Form.Label>
                                <Form.Control
                                    type="name"
                                    placeholder="Enter name"
                                    value={name}
                                    onChange={(e) => setName(e.target.value)}
                                ></Form.Control>
                            </Form.Group>
                            <Form.Group controlId='image'>
                                <Form.Label>Image</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Enter image url"
                                    value={image}
                                    onChange={(e) => setImage(e.target.value)}
                                ></Form.Control>
                                <Form.File
                                    id='image-file'
                                    label='Choose File'
                                    custom
                                    onChange={uploadFileHandler}
                                ></Form.File>
                                {uploading && <Loader />}
                            </Form.Group>
                            <Form.Group controlId='price'>
                                <Form.Label>Price</Form.Label>
                                <Form.Control
                                    type="price"
                                    placeholder="Enter price"
                                    value={price}
                                    onChange={(e) => setPrice(e.target.value)}
                                ></Form.Control>
                            </Form.Group>
                            <Form.Group controlId='description'>
                                <Form.Label>Description</Form.Label>
                                <Form.Control
                                    as="textarea"
                                    rows={3}
                                    type="description"
                                    placeholder="Enter description"
                                    value={description}
                                    onChange={(e) => setDescription(e.target.value)}
                                ></Form.Control>
                            </Form.Group>
                        </Col>
                        <Col md={6}>
                            <Form.Group controlId='countInStock'>
                                <Form.Label>Target Plant</Form.Label>
                                <Form.Control
                                    type="countInStock"
                                    placeholder="Enter target plant"
                                    value={target_plant}
                                    onChange={(e) => setTarget_plant(e.target.value)}
                                ></Form.Control>
                            </Form.Group>
                            <Form.Group controlId='quantity'>
                                <Form.Label>Quantity</Form.Label>
                                <Form.Control
                                    type="countInStock"
                                    placeholder="Enter qunatity"
                                    value={quantity}
                                    onChange={(e) => setQuantity(e.target.value)}
                                ></Form.Control>
                            </Form.Group>
                            <Form.Group controlId='serv1power'>
                                <Form.Label>Serv1 Power</Form.Label>
                                <Form.Control
                                    type="serv1power"
                                    placeholder="Enter serv1 power"
                                    value={serv1_power}
                                    onChange={(e) => setServ1_power(e.target.value)}
                                ></Form.Control>
                            </Form.Group>
                            <Button type="submit" variant="primary">Update</Button>
                        </Col>
                    </Row>
                </Form>
            </FormContainer>
        </Container>
    )
}

export default Service1ListEdit 
