import axios from 'axios'
import {
    PRODUCT_SERV1_LIST_REQUEST,
    PRODUCT_SERV1_LIST_SUCCESS,
    PRODUCT_SERV1_LIST_FAIL,
    PRODUCT_SERV1_DETAILS_REQUEST,
    PRODUCT_SERV1_DETAILS_SUCCESS,
    PRODUCT_SERV1_DETAILS_FAIL,
    SERV1_DELETE_REQUEST,
    SERV1_DELETE_SUCCESS,
    SERV1_DELETE_FAIL,
    SERV1_CREATE_REQUEST,
    SERV1_CREATE_SUCCESS,
    SERV1_CREATE_FAIL,
    SERV1_UPDATE_REQUEST,
    SERV1_UPDATE_SUCCESS,
    SERV1_UPDATE_FAIL,
    SERV1_UPDATE_RESET,
} from '../constants/productConstants.js'

export const listService1Products = () => async (dispatch) => {
    try {
        dispatch({ type: PRODUCT_SERV1_LIST_REQUEST })

        const { data } = await axios.get('/api/services1')

        dispatch({
            type: PRODUCT_SERV1_LIST_SUCCESS,
            payload: data
        })
    } catch (error) {
        dispatch({
            type: PRODUCT_SERV1_LIST_FAIL,
            payload:
                error.response && error.response.data.message
                    ? error.response.data.message
                    : error.message
        })
    }
}

export const listService1ProductsDetails = (id) => async (dispatch) => {
    try {
        dispatch({ type: PRODUCT_SERV1_DETAILS_REQUEST })

        const { data } = await axios.get(`/api/services1/${id}`)

        dispatch({
            type: PRODUCT_SERV1_DETAILS_SUCCESS,
            payload: data
        })
    } catch (error) {
        dispatch({
            type: PRODUCT_SERV1_DETAILS_FAIL,
            payload:
                error.response && error.response.data.message
                    ? error.response.data.message
                    : error.message
        })
    }
}

export const deleteService1Product = (id) => async (dispatch, getState) => {
    try {
        dispatch({ type: SERV1_DELETE_REQUEST })

        const { userLogin: { userInfo } } = getState()

        const config = {
            headers: {
                Authorization: `Bearer ${userInfo.token}`
            },
        }

        await axios.delete(`/api/services1/${id}`, config)

        dispatch({
            type: SERV1_DELETE_SUCCESS,
        })
    } catch (error) {
        dispatch({
            type: SERV1_DELETE_FAIL,
            payload:
                error.response && error.response.data.message
                    ? error.response.data.message
                    : error.message
        })
    }
}

export const createService1 = (id) => async (dispatch, getState) => {
    try {
        dispatch({ type: SERV1_CREATE_REQUEST })

        const { userLogin: { userInfo } } = getState()

        const config = {
            headers: {
                Authorization: `Bearer ${userInfo.token}`
            },
        }

        const { data } = await axios.post(`/api/services1`, {}, config)

        dispatch({
            type: SERV1_CREATE_SUCCESS,
            payload: data
        })
    } catch (error) {
        dispatch({
            type: SERV1_CREATE_FAIL,
            payload:
                error.response && error.response.data.message
                    ? error.response.data.message
                    : error.message
        })
    }
}

export const updateService1 = (serv1) => async (dispatch, getState) => {
    try {
        dispatch({ type: SERV1_UPDATE_REQUEST })

        const { userLogin: { userInfo } } = getState()

        const config = {
            headers: {
                'Content-type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            },
        }

        const { data } = await axios.put(`/api/services1/${serv1._id}`, serv1, config)

        dispatch({
            type: SERV1_UPDATE_SUCCESS,
            payload: data
        })

        dispatch({ type: SERV1_UPDATE_RESET })
        
    } catch (error) {
        dispatch({
            type: SERV1_UPDATE_FAIL,
            payload:
                error.response && error.response.data.message
                    ? error.response.data.message
                    : error.message
        })
    }
}