import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom';
import {
    Col,
    Container,
    Row,
    Image,
    ListGroup,
    Card,
    

} from 'react-bootstrap';
import './Service1Screen.css';

import { listService1ProductsDetails } from '../../actions/productServices1Actions'
import Loader from '../../components/Loader/Loader';
import Message from '../../components/Message/Message';
import Meta from '../../components/Helmet/Meta';

const Service1Product = ({ history, match }) => {

    const dispatch = useDispatch()

    const productServices1Details = useSelector(state => state.productServices1Details)
    const { loading, error, productServices1 } = productServices1Details

    useEffect(() => {
        dispatch(listService1ProductsDetails(match.params.id))
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [dispatch, match])

    return (
        <div className="productScreen">
            <Meta
                title=" | Serv1"
            />
            <Container>
                <Link className="btn btn-go-back btn-dark" to="/utilitiess/services1">GO BACK</Link>
                {
                    loading
                        ? <Loader />
                        : error
                            ? <Message variant='danger'>{error}</Message>
                            : (
                                <Row className="p-3 service2-product" >
                                    <Col md={6}>
                                        <Image className="mx-auto image-serv1" src={productServices1.image} alt={productServices1.name} width={200} />
                                    </Col>
                                    <Col md={3}>
                                        <ListGroup className="borderless" variant='flush'>
                                            <ListGroup.Item>
                                                <h2>{productServices1.name}</h2>
                                            </ListGroup.Item>
                                            <ListGroup.Item>
                                                <h4>Price: {productServices1.price}</h4>
                                            </ListGroup.Item>
                                            <ListGroup.Item>
                                            <p><span style={{fontWeight:"bold"}}>Description:</span><br /> {productServices1.description}</p>
                                            </ListGroup.Item>
                                            <ListGroup.Item>
                                                <p>Quantity Available: {productServices1.quantity}</p>
                                            </ListGroup.Item>
                                        </ListGroup>
                                    </Col>
                                    <Col md={3}>
                                        <Card>
                                            <ListGroup variant='flush'>
                                                <ListGroup.Item>
                                                    <Row>
                                                        <Col>Serv1 Power:</Col>
                                                        <Col><strong>{productServices1.serv1_power}</strong></Col>
                                                    </Row>
                                                </ListGroup.Item>
                                                <ListGroup.Item>
                                                    <Row>
                                                        <Col>Target Plant:</Col>
                                                        <Col>
                                                            {productServices1.target_plant}
                                                        </Col>
                                                    </Row>
                                                </ListGroup.Item>
                                                
                                               
                                            </ListGroup>
                                        </Card>
                                    </Col>
                                </Row>
                            )
                }
            </Container>
        </div>
    )
}

export default Service1Product
