import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { Container, Row, Button, Alert } from 'react-bootstrap'
import PurchaseServices2 from '../../components/PurchaseServices2/PurchaseServices2';
import './Utilities_ProductService2Styles.css'

import Message from '../../components/Message/Message';
import Loader from '../../components/Loader/Loader';

import { listService2Products } from '../../actions/productService2Actions'
import Meta from '../../components/Helmet/Meta';

const Utilities_ProductService2Screen = () => {
    const dispatch = useDispatch()

    const prodcutService2List = useSelector(state => state.prodcutService2List)
    const { loading, error, productServices2 } = prodcutService2List

    const [numberOfItems, setNumberOfItems] = useState(3);

    useEffect(() => {
        dispatch(listService2Products())
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [dispatch])

    const showMore = () => {
        if (numberOfItems + 3 <= productServices2.length) {
            setNumberOfItems(numberOfItems + 3)
        } else {
            setNumberOfItems(productServices2.length)
        }
    }

    return (

        <div className="ProductService2Screen">
            <Meta
                title=" | Utilities  services2"
            />
            <Container>
                <h1 className="p-3" style={{ textAlign: 'center' }}>Latest services2</h1>
                {
                    loading
                        ? <Loader />
                        : error ? <Message variant='danger'>{error}</Message>
                            :
                            (<Row>
                                {
                                    productServices2
                                        .slice(0, numberOfItems)
                                        .map(service2 => (
                                            <PurchaseServices2
                                                key={service2._id}
                                                _id={service2._id}
                                                name={service2.name}
                                                image={service2.image}
                                                rating={service2.rating}
                                                reviews={service2.numReviews}
                                                price={service2.price}
                                            />
                                        ))
                                }
                                {
                                    numberOfItems >= productServices2.length
                                        ? <Alert style={{ backgroundColor: 'red' }} className="col-md-12 text-center"></Alert>
                                        : ''
                                }
                                <Button className="col-md-12 text-center" variant="success outline-dark" onClick={showMore}> </Button>
                            </Row>)
                }
            </Container>
        </div>
    )
}

export default Utilities_ProductService2Screen 
