import React, { useEffect } from 'react'
import { LinkContainer } from 'react-router-bootstrap'
import {
    Table,
    Button,
    Container,
    Col,
    Row
} from 'react-bootstrap'
import { useHistory } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../../../Message/Message'
import Loader from '../../../Loader/Loader'
import { listService1Products, deleteService1Product, createService1 } from '../../../../actions/productServices1Actions'
import { SERV1_CREATE_RESET } from '../../../../constants/productConstants'

const Service2List = () => {

    const dispatch = useDispatch()
    let history = useHistory()

    const productServices1List = useSelector(state => state.productServices1List)
    const { loading: loadingServ1, error: errorServ1, productServices1 } = productServices1List

    const productServices1Delete = useSelector(state => state.productServices1Delete)
    const { loading: loadingDelete, error: errorDelete, success: successDelete } = productServices1Delete

    const Services1Create = useSelector(state => state.Services1Create)
    const { loading: loadingCreate, error: errorCreate, success: successCreate, product: productCreate } = Services1Create

    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin

    useEffect(() => {
        dispatch({ type: SERV1_CREATE_RESET })
        if (!userInfo.isAdmin && !userInfo) {
            history.push('/login')
        } else {
            if (successCreate) {
                history.push(`/admin/productlist/serv1/${productCreate._id}/edit`)
            } else {
                dispatch(listService1Products())
            }
        }
    }, [dispatch, history, userInfo, successDelete, successCreate, productCreate])

    const deleteHandler = (id) => {
        if (window.confirm('Are you sure')) {
            dispatch(deleteService1Product(id))
        }
    }

    const createServ1ProductHandler = () => {
        dispatch(createService1())
    }

    return (
        <Container>
            <Row>
                <Col>
                    <h1 style={{ marginBottom: '20px' }}> serv 1</h1>
                </Col>
                <Col className="text-right">
                    <Button className='my-3' onClick={createServ1ProductHandler}>
                        <i className='fas fa-plus'></i> Create Product
                    </Button>
                </Col>
            </Row>
            { loadingCreate && <Loader />}
            { errorCreate && <Message variant='danger'>{errorCreate}</Message>}
            { loadingDelete && <Loader />}
            { errorDelete && <Message variant='danger'>{errorDelete}</Message>}
            {loadingServ1 ? <Loader />
                : errorServ1 ? <Message variant='danger'>{errorServ1}</Message>
                    : (
                        <Table style={{ marginBottom: '50px' }} striped bordered hover responsive className='table-sm'>
                            <thead>
                                <tr>
                                    <td>ID</td>
                                    <td>NAME</td>
                                    <td>TARGET PLANT</td>
                                    <td>SERV1 POWER</td>
                                    <td>EDIT / DELETE</td>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    productServices1.map(serv1 => (
                                        <tr key={serv1._id}>
                                            <td>{serv1._id}</td>
                                            <td>{serv1.name}</td>
                                            <td>{serv1.target_plant}</td>
                                            <td>{serv1.serv1_power}</td>
                                            <td>
                                                <LinkContainer to={`/admin/productlist/serv1/${serv1._id}/edit`}>
                                                    <Button variant="light" className="btn btn-sm">
                                                        <i className="fas fa-edit"></i>
                                                    </Button>
                                                </LinkContainer>
                                                <Button
                                                    variant="danger"
                                                    className="btn-sm mr-2"
                                                    onClick={() => deleteHandler(serv1._id)}
                                                >
                                                    <i className="fas fa-trash-alt"></i>
                                                </Button>
                                            </td>
                                        </tr>
                                    ))
                                }
                            </tbody>
                        </Table>
                    )
            }
        </Container>
    )
}

export default Service2List
