import React, { useEffect } from 'react'
import { LinkContainer } from 'react-router-bootstrap'
import {
    Table,
    Button,
    Row,
    Col,
    Container
} from 'react-bootstrap'
import { useHistory } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../../../Message/Message'
import Loader from '../../../Loader/Loader'
import { listActivitieProducts, deleteActivitieProduct, createActivitie } from './../../../../actions/activitieProductAction'
import { ACTIVITIE_CREATE_RESET } from '../../../../constants/productConstants'

const ActivitieList = () => {

    const dispatch = useDispatch()
    let history = useHistory()

    const activitieProductList = useSelector(state => state.activitieProductList)
    const { loading: loadingActivitie, error: errorActivitie, activitieProducts } = activitieProductList

    const activitieProductDelete = useSelector(state => state.activitieProductDelete)
    const { loading: deleteLoadingActivitie, error: errorDeleteActivitie, success: successDelete } = activitieProductDelete

    const activitieCreate = useSelector(state => state.activitieCreate)
    const {
        loading: createLoadingActivitie,
        error: errorcreateActivitie,
        success: successCreate,
        product: activitieProduct
    } = activitieCreate

    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin

    useEffect(() => {
        dispatch({ type: ACTIVITIE_CREATE_RESET })
        if (!userInfo.isAdmin && !userInfo) {
            history.push('/login')
        } else {
            if (successCreate) {
                history.push(`/admin/productlist/activitie/${activitieProduct._id}/edit`)
            } else {
                dispatch(listActivitieProducts())
            }
        }
    }, [dispatch, history, userInfo, successDelete, successCreate, activitieProduct])

    const deleteHandler = (id) => {
        if (window.confirm('Are you sure')) {
            dispatch(deleteActivitieProduct(id))
        }
    }

    const createActivitieProductHandler = () => {
        dispatch(createActivitie())
    }

    return (
        <Container>
            <Row>
                <Col>
                    <h1 style={{ marginBottom: '20px' }}>Activitie</h1>
                </Col>
                <Col className="text-right">
                    <Button className='my-3' onClick={createActivitieProductHandler}>
                        <i className='fas fa-plus'></i> Create Product
                    </Button>
                </Col>
            </Row>
            { createLoadingActivitie && <Loader />}
            { errorcreateActivitie && <Message variant='danger'>{errorcreateActivitie}</Message>}
            { deleteLoadingActivitie && <Loader />}
            { errorDeleteActivitie && <Message variant='danger'>{errorDeleteActivitie}</Message>}
            {loadingActivitie ? <Loader />
                : errorActivitie ? <Message variant='danger'>{errorActivitie}</Message>
                    : (
                        <Table style={{ marginBottom: '50px' }} striped bordered hover responsive className='table-sm'>
                            <thead>
                                <tr>
                                    <td>ID</td>
                                    <td>NAME</td>
                                    <td>NOTE</td>
                                    <td>EDIT / DELETE</td>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    activitieProducts.map(activitie => (
                                        <tr key={activitie._id}>
                                            <td>{activitie._id}</td>
                                            <td>{activitie.name}</td>
                                            <td>{activitie.note}</td>
                                            
                                            <td>
                                                <LinkContainer to={`/admin/productlist/activitie/${activitie._id}/edit`}>
                                                    <Button variant="light" className="btn btn-sm">
                                                        <i className="fas fa-edit"></i>
                                                    </Button>
                                                </LinkContainer>
                                                <Button
                                                    variant="danger"
                                                    className="btn-sm mr-2"
                                                    onClick={() => deleteHandler(activitie._id)}
                                                >
                                                    <i className="fas fa-trash-alt"></i>
                                                </Button>
                                            </td>
                                        </tr>
                                    ))
                                }
                            </tbody>
                        </Table>
                    )
            }
        </Container>
    )
}

export default ActivitieList
