import React, { useEffect } from 'react'
import { LinkContainer } from 'react-router-bootstrap'
import {
    Table,
    Button,
    Row,
    Col,
    Container
} from 'react-bootstrap'
import { useHistory } from "react-router-dom"
import { useDispatch, useSelector } from 'react-redux'
import Message from '../../../Message/Message'
import Loader from '../../../Loader/Loader'
import { listService2Products, deleteService2Products, createService2Products } from '../../../../actions/productService2Actions'
import { SERVICE2_CREATE_RESET } from '../../../../constants/productConstants'

const Service2List = () => {

    const dispatch = useDispatch()
    let history = useHistory()

    const prodcutService2List = useSelector(state => state.prodcutService2List)
    const { loading: loadingService2, error: errorService2, productServices2 } = prodcutService2List

    const prodcutService2Delete = useSelector(state => state.prodcutService2Delete)
    const { success: successService2Delete, loading: loadingDelete, error: errorDelete } = prodcutService2Delete

    const service2Create = useSelector(state => state.service2Create)
    const {
        success: successService2Create,
        loading: loadingCreate,
        error: errorCreate,
        product: productCreate
    } = service2Create

    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin

    useEffect(() => {
        dispatch({ type: SERVICE2_CREATE_RESET })
        if (!userInfo.isAdmin && !userInfo) {
            history.push('/login')
        } else {
            if (successService2Create) {
                history.push(`/admin/productlist/service2/${productCreate._id}/edit`)
            } else {
                dispatch(listService2Products())
            }
        }
    }, [dispatch, history, userInfo, successService2Delete, successService2Create, productCreate])

    const deleteHandler = (id) => {
        if (window.confirm('Are you sure')) {
            dispatch(deleteService2Products(id))
        }
    }

    const createService2ProductHandler = () => {
        dispatch(createService2Products())
    }

    return (
        <Container>
            <Row>
                <Col>
                    <h1 style={{ marginBottom: '20px' }}>services2</h1>
                </Col>
                <Col className="text-right">
                    <Button className='my-3' onClick={createService2ProductHandler}>
                        <i className='fas fa-plus'></i> Create Product
                    </Button>
                </Col>
            </Row>
            { loadingCreate && <Loader />}
            { loadingDelete && <Loader />}
            { errorCreate && <Message variant='danger'>{errorCreate}</Message>}
            { errorDelete && <Message variant='danger'>{errorDelete}</Message>}
            {loadingService2 ? <Loader />
                : errorService2 ? <Message variant='danger'>{errorService2}</Message>
                    : (
                        <Table style={{ marginBottom: '50px' }} striped bordered hover responsive className='table-sm'>
                            <thead>
                                <tr>
                                    <td>ID</td>
                                    <td>NAME</td>
                                    <td>CATEGORY</td>
                                    <td>PRICE</td>
                                    <td>EDIT / DELETE</td>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    productServices2.map(productService2 => (
                                        <tr key={productService2._id}>
                                            <td>{productService2._id}</td>
                                            <td>{productService2.name}</td>
                                            <td>{productService2.category}</td>
                                            <td>{productService2.price}</td>
                                            <td>
                                                <LinkContainer to={`/admin/productlist/service2/${productService2._id}/edit`}>
                                                    <Button variant="light" className="btn btn-sm">
                                                        <i className="fas fa-edit"></i>
                                                    </Button>
                                                </LinkContainer>
                                                <Button
                                                    variant="danger"
                                                    className="btn-sm mr-2"
                                                    onClick={() => deleteHandler(productService2._id)}
                                                >
                                                    <i className="fas fa-trash-alt"></i>
                                                </Button>
                                            </td>
                                        </tr>
                                    ))
                                }
                            </tbody>
                        </Table>
                    )
            }
        </Container>
    )
}

export default Service2List
