import React, { useState, useEffect } from 'react'
import axios from 'axios'
import {
    Form,
    Button,
    Container,
    Row,
    Col
} from 'react-bootstrap'
import { Link, useHistory } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../../../components/Message/Message'
import Loader from '../../../components/Loader/Loader'
import FormContainer from '../../../components/FormContainer/FormContainer'
import { listActivitieProductsDetails, updateActivitie } from '../../../actions/activitieProductAction'
import { ACTIVITIE_UPDATE_RESET } from '../../../constants/productConstants'
import Meta from '../../../components/Helmet/Meta'

const ActivitieListEdit = ({ match }) => {

    const [name, setName] = useState('')
    const [image, setImage] = useState('')
    const [note, setNote] = useState('')
    const [uploading, setUploading] = useState(false)

    const productId = match.params.id

    const dispatch = useDispatch()
    let history = useHistory()

    const activitieProductDetails = useSelector(state => state.activitieProductDetails)
    const { loading, activitieProduct, error } = activitieProductDetails

    const activitieUpdate = useSelector(state => state.activitieUpdate)
    const { loading: loadingUpdate, success: successUpdate, error: errorUpdate } = activitieUpdate

    useEffect(() => {
        if (successUpdate) {
            dispatch({ type: ACTIVITIE_UPDATE_RESET })
            history.push('/admin/productlist')
        } else {
            if (!activitieProduct.name || activitieProduct._id !== productId) {
                dispatch(listActivitieProductsDetails(productId))
            } else {
                setName(activitieProduct.name)
                setNote(activitieProduct.note)
                setImage(activitieProduct.image)
                
            }
        }
    }, [history, activitieProduct, dispatch, productId, successUpdate])

    const submitHandler = (e) => {
        e.preventDefault()
        dispatch(updateActivitie({
            _id: productId,
            name: name,
            image: image,
            note: note,
        }))
    }

    const uploadFileHandler = async (e) => {
        const file = e.target.files[0]
        const formData = new FormData()
        formData.append('image', file)
        setUploading(true)

        try {
            const config = {
                headers: {
                    'Content-type': 'multipart/form-data'
                }
            }

            const { data } = await axios.post('/api/upload', formData, config)

            setImage(data)
            setUploading(false)

        } catch (error) {
            console.error(error)
            setUploading(false)
        }
    }

    return (
        <Container style={{ marginBottom: '50px' }}>
            <Meta
                title=" | Admin Activitie Edit"
            />
            <FormContainer>
                <h2 style={{ marginTop: '120px', textAlign: 'center' }}>Activitie Profile</h2>
                <Link to='/admin/productlist' className='btn btn-light my-3'>
                    GO BACK
                </Link>
                {loadingUpdate && <Loader />}
                {errorUpdate && <Message variant='danger'>{errorUpdate}</Message>}
                {loading && <Loader />}
                {error && <Message variant='danger'>{error}</Message>}
                {successUpdate && <Message variant='success'>Profile Updated!</Message>}
                <Form onSubmit={submitHandler}>
                    <Row>
                        <Col md={6}>
                            <Form.Group controlId='name'>
                                <Form.Label>Name</Form.Label>
                                <Form.Control
                                    type="name"
                                    placeholder="Enter name"
                                    value={name}
                                    onChange={(e) => setName(e.target.value)}
                                ></Form.Control>
                            </Form.Group>
                            <Form.Group controlId='image'>
                                <Form.Label>Image</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Enter image url"
                                    value={image}
                                    onChange={(e) => setImage(e.target.value)}
                                ></Form.Control>
                                <Form.File
                                    id='image-file'
                                    label='Choose File'
                                    custom
                                    onChange={uploadFileHandler}
                                ></Form.File>
                                {uploading && <Loader />}
                            </Form.Group>
                            <Form.Group controlId='sellerName'>
                                <Form.Label>note</Form.Label>
                                <Form.Control
                                    type="sellerName"
                                    placeholder="Enter seller name"
                                    value={note}
                                    onChange={(e) => setNote(e.target.value)}
                                ></Form.Control>
                            </Form.Group>
                             <Button type="submit" variant="primary">Update</Button>
                        </Col>
                    </Row>
                </Form>
            </FormContainer>
        </Container>
    )
}

export default ActivitieListEdit 
