import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
    Container,
    Row,
    Button,
    Alert
} from 'react-bootstrap';
import ActivitieProducts from '../../components/ActivitieProducts/ActivitieProducts'
import { listActivitieProducts } from '../../actions/activitieProductAction.js'
import Message from '../../components/Message/Message';
import Loader from '../../components/Loader/Loader';
import './ActivitieStyles.css'
import Meta from '../../components/Helmet/Meta';

const ActivitieScreen = () => {

    const dispatch = useDispatch();

    const activitieProductList = useSelector(state => state.activitieProductList)
    const { loading, activitieProducts, error } = activitieProductList

    const [numberOfItems, setNumberOfItems] = useState(3);

    useEffect(() => {
        dispatch(listActivitieProducts())
    }, [dispatch])

    const showMore = () => {
        if (numberOfItems + 3 <= activitieProducts.length) {
            setNumberOfItems(numberOfItems + 3)
        } else {
            setNumberOfItems(activitieProducts.length)
        }
    }

    return (
        <div className="activitieProductScreen">
            <Meta
                title=" | Activitie"
            />
            <Container className='activitieContainer'>
                <h1 className="title"> </h1>
                <h4 className="activitie-title">
                                                           </h4>
                <br />
                {
                    loading ? <Loader />
                        : error ? <Message variant='danger'>{error}</Message>
                            : (
                                <Row>
                                    {
                                        activitieProducts
                                            .slice(0, numberOfItems)
                                            .map(activitie => (
                                                <ActivitieProducts
                                                    key={activitie._id}
                                                    _id={activitie._id}
                                                    name={activitie.name}
                                                    note={activitie.note}
                                                    image={activitie.image}
                                                     />
                                            ))
                                    }
                                    {
                                        numberOfItems >= activitieProducts.length
                                            ? <Alert style={{ backgroundColor: 'red' }} className="col-md-12 text-center"></Alert>
                                            : ''
                                    }
                                    <Button className="col-md-12 text-center" variant="success outline-dark" onClick={showMore}> </Button>
                                </Row>
                            )
                }
            </Container>
        </div>
    )
}

export default ActivitieScreen
