import React, { useState, useEffect } from 'react'

import {
    Form,
    Button,
    Container,
    Row,
    Col
} from 'react-bootstrap'
import { Link, useHistory } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import Message from './../Message/Message'
import Loader from './../Loader/Loader'
import FormContainer from './../FormContainer/FormContainer'
import { updateSupplierProduct, getroductsDetails } from './../../actions/supplierProduct'
import { SUPPLIER_PRODUCT_UPDATE_RESET } from './../../constants/supplierConstant'
import Meta from '../Helmet/Meta'

const SupplierProductEdit = ({ match }) => {

    const [name, setName] = useState('')
    const [description, setDescription] = useState('')
    const [date, setDate] = useState('')
    const [address, setAddress] = useState('')
    const [level, setLevel] = useState('')
    const [phonenumber, setPhonenumber] = useState('')
    

    const productId = match.params.id

    const dispatch = useDispatch()
    let history = useHistory()

    const UtilitiesProductDetails = useSelector(state => state.UtilitiesProductDetails)
    const { loading, product, error } = UtilitiesProductDetails

    const utilitiesProductUpdate = useSelector(state => state.utilitiesProductUpdate)
    const { loading: loadingUpdate, success: successUpdate, error: errorUpdate } = utilitiesProductUpdate

    useEffect(() => {
        if (successUpdate) {
            dispatch({ type: SUPPLIER_PRODUCT_UPDATE_RESET })
            history.push('/profile')
        } else {
            if (!product.name || product._id !== productId) {
                dispatch(getroductsDetails(productId))
            } else {
                setName(product.name)
                setDescription(product.description)
                setDate(product.date)
                setAddress(product.address)
                setLevel(product.level)
                setPhonenumber(product.phonenumber)
            }
        }
    }, [history, product, dispatch, productId, successUpdate])

    const submitHandler = (e) => {
        e.preventDefault()
        dispatch(updateSupplierProduct({
            _id: productId,
            name,
            date,
            address,
            level,
            phonenumber,
            description
        }))
    }

   

    return (
        <Container style={{ marginBottom: '50px' }}>
            <Meta
                title=" | Supplier Edit"
            />
            <FormContainer>
                <h2 style={{ marginTop: '100px', textAlign: 'center' }}>Product Profile</h2>
                <Link to='/profile' className='btn btn-light my-3'>
                    GO BACK
                </Link>
                {loading && <Loader />}
                {error && <Message variant='danger'>{error}</Message>}
                {loadingUpdate && <Loader />}
                {errorUpdate && <Message variant='danger'>{errorUpdate}</Message>}
                {successUpdate && <Message variant='success'>Profile Updated!</Message>}
                <Form onSubmit={submitHandler}>
                <Row>
                    <Col md={5}>
                        <Form.Group controlId='name'>
                            <Form.Label>   <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="name"
                                placeholder=" "
                                value={name}
                                required
                                onChange={(e) => setName(e.target.value)}
                            ></Form.Control>
                        </Form.Group>

                        <Form.Group controlId="date">
                            <Form.Label>   <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="date" clrDate
                                placeholder="  "
                                value={date}
                                required
                                onChange={(e) => setDate(e.target.value)}
                            ></Form.Control>
                           
                        </Form.Group>
                        <Form.Group controlId='address'>
                            <Form.Label>  <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="address"
                                as="textarea" rows={1}
                                placeholder= " "
                                value={address}
                                required
                                onChange={(e) => setAddress(e.target.value)}
                            ></Form.Control>
                        </Form.Group>


                        <Form.Group controlId='level'>
                            <Form.Label>  <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="level"
                                placeholder="   "
                                value={level}
                                required
                                onChange={(e) => setLevel(e.target.value)}
                            ></Form.Control>
                        </Form.Group>
                    </Col>
                    <Col md={5}>
                        <Form.Group controlId='phonenumber'>
                            <Form.Label>  <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="phonenumber"
                                placeholder= "  "
                                value={phonenumber}
                                required
                                onChange={(e) => setPhonenumber(e.target.value)}
                            ></Form.Control>
                        </Form.Group>
                        <Form.Group controlId='description'>
                            <Form.Label>  <span style={{ color: 'red' }}>*</span></Form.Label>
                        
                        
                            <Form.Control
                                  type="description" 
                                  value={description}
                                  required
                                  onChange={(e) => setDescription(e.target.value)}
                            >
                            </Form.Control>
                        </Form.Group>
                        <br />
                        <Button type="submit" variant="primary"></Button>
                    </Col>
                </Row>
                </Form>
            </FormContainer>
        </Container>
    )
}

export default SupplierProductEdit 
