import React, { useState, useEffect } from 'react'

import {
    Form,
    Button,
    Row,
    Col
} from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../Message/Message'
import Loader from '../Loader/Loader'
import FormContainer from '../FormContainer/FormContainer'
import { registerStudent } from '../../actions/userActions'
import Meta from '../Helmet/Meta'

const RegisterStudent = ({ location, history }) => {

    const [name, setName] = useState('')
    const [description, setDescription] = useState('')
    const [date, setDate] = useState('')
    const [address, setAddress] = useState('')
    const [level, setLevel] = useState('')
    const [phonenumber, setPhonenumber] = useState('')

    
    const [message] = useState(null)


    const dispatch = useDispatch()

    const userRegister = useSelector(state => state.userRegister)
    const { loading, userInfo, error } = userRegister
    const redirect = location.search ? location.search.split('=')[1] : '/'

    useEffect(() => {
        if (userInfo) {
            history.push(redirect)
        }
    }, [userInfo, history, redirect])

  
    const submitHandler = (e) => {
        e.preventDefault()
            dispatch(registerStudent({name, date, address, level, phonenumber, description}))
        
       

    }


    return (

        <FormContainer>
            <Meta
                title=" | Register Student"
            />
            <h1 style={{ marginTop: '120px' }}>Sign Up</h1>
        
            {message && <Message variant='danger'>{message}</Message>}
            {error && <Message variant='danger'>{error}</Message>}

            {loading && <Loader />}
            <Form onSubmit={submitHandler}>
            <Row>
                    <Col md={5}>
                        <Form.Group controlId='name'>
                            <Form.Label>   <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="name"
                                placeholder=" "
                                value={name}
                                required
                                onChange={(e) => setName(e.target.value)}
                            ></Form.Control>
                        </Form.Group>
                        <Form.Group controlId="date">
                            <Form.Label>   <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="date" 
                                placeholder="  "
                                value={date}
                                required
                                onChange={(e) => setDate(e.target.value)}
                            ></Form.Control>
                           
                        </Form.Group>
                        <Form.Group controlId='address'>
                            <Form.Label>  <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="address"
                                as="textarea" rows={1}
                                placeholder= " "
                                value={address}
                                required
                                onChange={(e) => setAddress(e.target.value)}
                            ></Form.Control>
                        </Form.Group>


                        <Form.Group controlId='level'>
                            <Form.Label>  <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="level"
                                placeholder="   "
                                value={level}
                                required
                                onChange={(e) => setLevel(e.target.value)}
                            ></Form.Control>
                        </Form.Group>
                    </Col>
                    <Col md={5}>
                        <Form.Group controlId='phonenumber'>
                            <Form.Label>  <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="phonenumber"
                                placeholder= "  "
                                value={phonenumber}
                                required
                                onChange={(e) => setPhonenumber(e.target.value)}
                            ></Form.Control>
                        </Form.Group>
                        <Form.Group controlId='description'>
                            <Form.Label>  <span style={{ color: 'red' }}>*</span></Form.Label>
                        
                        
                            <Form.Control
                                  type="description" 
                                  value={description}
                                  required
                                  onChange={(e) => setDescription(e.target.value)}
                            >
                            </Form.Control>
                        </Form.Group>
                        <br />
                        <Button type="submit" variant="primary"></Button>
                    </Col>
                </Row>
            </Form>
           
        </FormContainer>
    )
}

export default RegisterStudent
