import React from 'react'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import Footer from './components/Footer/Footer';
import Header from './components/Header/Header';
import LoginComponent from './components/Login/LoginComponent';
import Register from './components/Register/Register';
import RegisterStudent from './components/Register/RegisterStudent';

import HomeScreen from './screens/Home/HomeScreen';
import Activitie from './screens/Activitie/ActivitieScreen';
import Utilities from './screens/Utilities/UtilitiesScreen';
import student from './screens/Supplier/SupplierScreen';
import ProfileScreen from './screens/Profile/ProfileScreen';
import DashboardScreen from './screens/Dashboard/DashboardScreen';
import AdminProfileScreen from './screens/Dashboard/ProfileScreen';
import Utilities_ProductService2 from './screens/Utilities_ProductService2/Utilities_ProductService2Screen';
import Service2ProductScreen from './screens/Product_Service2/Service2ProductScreen';
import Utilities_LendScreen from './screens/Utilities_Service1/Utilities_LendScreen';
import Service1Product from './screens/Product_Service1/Service1Product';
import ActivitieProductDetailScreen from './screens/Product_Activitie/ActivitieProductDetailScreen'
import UserListScreen from './screens/Dashboard/UserListScreen';
import UserEditScreen from './screens/UserEdit/UserEditScreen';
import ProductListScreen from './screens/Dashboard/ProductListScreen'
import Service2ListEdit from './screens/ProductListEdit/Service2ListEdit/Service2ListEdit'
import Serv1ListEdit from './screens/ProductListEdit/Serv1ListEdit/Serv1ListEdit'
import ActivitieListEdit from './screens/ProductListEdit/ActivitieListEdit/ActivitieListEdit'
import SupplierProductEdit from './components/SupplierProductEdit/SupplierProductEdit'
import HarvestScreen from './screens/Dashboard/HarvestScreen';
import UtilitiesProduct from './screens/UtilitiesProducts/UtilitiesProducts';



const Layout = () => {
    return (
        <>
            <Router>
                <Header />
                <Switch>
                    <Route exact path="/" component={HomeScreen} />
                    <Route exact path="/utilities" component={Utilities} />
                    <Route exact path="/activitie" component={Activitie} />
                    <Route exact path="/student" component={student} />
                    <Route exact path="/utilitiess/sellMeterial" component={LoginComponent} />
                    <Route exact path="/login" component={LoginComponent} />
                    <Route exact path="/register" component={Register} />
                    <Route exact path="/registerStudent" component={RegisterStudent}/>
                    <Route exact path="/profile" component={ProfileScreen} />
                                        

                    <Route exact path="/admin/userList" component={UserListScreen} />
                    <Route exact path="/admin/dashboard" component={DashboardScreen} />
                    <Route exact path="/admin/profile" component={AdminProfileScreen} />
                    <Route exact path="/admin/productlist" component={ProductListScreen} />
                    <Route exact path="/admin/supplierproducts" component={HarvestScreen} />
                    <Route exact path="/admin/productlist/service2/:id/edit" component={Service2ListEdit} />
                    <Route exact path="/admin/productlist/serv1/:id/edit" component={Serv1ListEdit} />
                    <Route exact path="/admin/productlist/activitie/:id/edit" component={ActivitieListEdit} />
                    <Route exact path="/admin/user/:id/edit" component={UserEditScreen} />
                    <Route exact path="/supplierproducts/:id/review" component={UtilitiesProduct} />
                    <Route exact path="/supplierproducts/:id/edit" component={SupplierProductEdit} />
            
                    <Route exact path="/utilitiess/purchaseServices2" component={Utilities_ProductService2} />
                    <Route exact path="/utilitiess/purchaseServices2/:id" component={Service2ProductScreen} />
                    <Route exact path="/utilitiess/services1" component={Utilities_LendScreen} />
                    <Route exact path="/utilitiess/services1/:id" component={Service1Product} />
                    <Route exact path="/activitie/:id" component={ActivitieProductDetailScreen} />
                </Switch>
                <Footer />
            </Router>
        </>
    )
}

export default Layout
