import axios from 'axios'
import {
    ACTIVITIE_PRODUCT_LIST_REQUEST,
    ACTIVITIE_PRODUCT_LIST_SUCCESS,
    ACTIVITIE_PRODUCT_LIST_FAIL,
    ACTIVITIE_PRODUCT_DETAILS_REQUEST,
    ACTIVITIE_PRODUCT_DETAILS_SUCCESS,
    ACTIVITIE_PRODUCT_DETAILS_FAIL,
    ACTIVITIE_DELETE_REQUEST,
    ACTIVITIE_DELETE_SUCCESS,
    ACTIVITIE_DELETE_FAIL,
    ACTIVITIE_CREATE_REQUEST,
    ACTIVITIE_CREATE_SUCCESS,
    ACTIVITIE_CREATE_FAIL,
    ACTIVITIE_UPDATE_REQUEST,
    ACTIVITIE_UPDATE_SUCCESS,
    ACTIVITIE_UPDATE_FAIL,
    ACTIVITIE_UPDATE_RESET,
} from '../constants/productConstants'

export const listActivitieProducts = () => async (dispatch) => {
    try {
        dispatch({ type: ACTIVITIE_PRODUCT_LIST_REQUEST })

        const { data } = await axios.get('/api/activitie')

        dispatch({
            type: ACTIVITIE_PRODUCT_LIST_SUCCESS,
            payload: data
        })
    } catch (error) {
        dispatch({
            type: ACTIVITIE_PRODUCT_LIST_FAIL,
            payload:
                error.response && error.response.data.message
                    ? error.response.data.message
                    : error.message
        })
    }
}

export const listActivitieProductsDetails = (id) => async (dispatch) => {
    try {
        dispatch({ type: ACTIVITIE_PRODUCT_DETAILS_REQUEST })

        const { data } = await axios.get(`/api/activitie/${id}`)

        dispatch({
            type: ACTIVITIE_PRODUCT_DETAILS_SUCCESS,
            payload: data
        })
    } catch (error) {
        dispatch({
            type: ACTIVITIE_PRODUCT_DETAILS_FAIL,
            payload:
                error.response && error.response.data.message
                    ? error.response.data.message
                    : error.message
        })
    }
}

export const deleteActivitieProduct = (id) => async (dispatch, getState) => {
    try {
        dispatch({ type: ACTIVITIE_DELETE_REQUEST })

        const { userLogin: { userInfo } } = getState()

        const config = {
            headers: {
                Authorization: `Bearer ${userInfo.token}`
            },
        }

        await axios.delete(`/api/activitie/${id}`, config)

        dispatch({
            type: ACTIVITIE_DELETE_SUCCESS,
        })
    } catch (error) {
        dispatch({
            type: ACTIVITIE_DELETE_FAIL,
            payload:
                error.response && error.response.data.message
                    ? error.response.data.message
                    : error.message
        })
    }
}

export const createActivitie = (id) => async (dispatch, getState) => {
    try {
        dispatch({ type: ACTIVITIE_CREATE_REQUEST })

        const { userLogin: { userInfo } } = getState()

        const config = {
            headers: {
                Authorization: `Bearer ${userInfo.token}`
            },
        }

        const { data } = await axios.post(`/api/activitie/`, {}, config)

        dispatch({
            type: ACTIVITIE_CREATE_SUCCESS,
            payload: data
        })
    } catch (error) {
        dispatch({
            type: ACTIVITIE_CREATE_FAIL,
            payload:
                error.response && error.response.data.message
                    ? error.response.data.message
                    : error.message
        })
    }
}

export const updateActivitie = (activitie) => async (dispatch, getState) => {
    try {
        dispatch({ type: ACTIVITIE_UPDATE_REQUEST })

        const { userLogin: { userInfo } } = getState()

        const config = {
            headers: {
                'Content-type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            },
        }

        const { data } = await axios.put(`/api/activitie/${activitie._id}`, activitie, config)

        dispatch({
            type: ACTIVITIE_UPDATE_SUCCESS,
            payload: data
        })

        dispatch({ type: ACTIVITIE_UPDATE_RESET })

    } catch (error) {
        dispatch({
            type: ACTIVITIE_UPDATE_FAIL,
            payload:
                error.response && error.response.data.message
                    ? error.response.data.message
                    : error.message
        })
    }
}