import React, { useState } from 'react';


import {
    Form,
    Button,
    Row,
    Col,
    Container,
} from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'

import Message from './../../components/Message/Message'
import Loader from './../../components/Loader/Loader'
import { createSupplierProduct } from '../../actions/supplierProduct'

const AddSupplierProduct = () => {

    const [name, setName] = useState('')
    const [date, setDate] = useState('')
    const [address, setAddress] = useState('')
    const [description, setDescription] = useState('')
    const [phonenumber, setPhonenumber] = useState('')
    const [level, setLevel] = useState('')

    const dispatch = useDispatch()

    const productCreate = useSelector(state => state.productCreate)
    const { loading, success, error } = productCreate

    

  

    const submitHandler = (e) => {
        e.preventDefault()
        dispatch(createSupplierProduct({
            name,
            date,
            address,
            level,
            phonenumber,
            description
        })
        )

        setName('')
        setDate('')
        setAddress('')
        setDescription('')
        setLevel('')
        setPhonenumber('')
    }

    return (
        <Container>
            {success && <Message variant='success'>   </Message>}
            {error && <Message variant='danger'>  </Message>}
            {loading && <Loader />}
            <Form onSubmit={submitHandler}>
                <Row>
                    <Col md={5}>
                        <Form.Group controlId='name'>
                            <Form.Label>   <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="name"
                                placeholder=" "
                                value={name}
                                required
                                onChange={(e) => setName(e.target.value)}
                            ></Form.Control>
                        </Form.Group>
                        <Form.Group controlId="date">
                            <Form.Label>   <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="date" clrDate
                                placeholder="  "
                                value={date}
                                required
                                onChange={(e) => setDate(e.target.value)}
                            ></Form.Control>
                           
                        </Form.Group>
                        <Form.Group controlId='address'>
                            <Form.Label>  <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="address"
                                as="textarea" rows={1}
                                placeholder= " "
                                value={address}
                                required
                                onChange={(e) => setAddress(e.target.value)}
                            ></Form.Control>
                        </Form.Group>


                        <Form.Group controlId='level'>
                            <Form.Label>  <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="level"
                                placeholder="   "
                                value={level}
                                required
                                onChange={(e) => setLevel(e.target.value)}
                            ></Form.Control>
                        </Form.Group>
                    </Col>
                    <Col md={5}>
                        <Form.Group controlId='phonenumber'>
                            <Form.Label>  <span style={{ color: 'red' }}>*</span></Form.Label>
                            <Form.Control
                                type="phonenumber"
                                placeholder= "  "
                                value={phonenumber}
                                required
                                onChange={(e) => setPhonenumber(e.target.value)}
                            ></Form.Control>
                        </Form.Group>
                        <Form.Group controlId='description'>
                            <Form.Label>  <span style={{ color: 'red' }}>*</span></Form.Label>
                        
                        
                            <Form.Control
                                  type="description" 
                                  value={description}
                                  required
                                  onChange={(e) => setDescription(e.target.value)}
                            >
                            </Form.Control>
                        </Form.Group>
                        <br />
                        <Button type="submit" variant="primary"></Button>
                    </Col>
                </Row>
            </Form>
        </Container>
    )
}

export default AddSupplierProduct

