import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { Container, Row, Button, Alert } from 'react-bootstrap'
import Services1 from './../../components/Services1//Services1';

import './Utilities_Service1.css'
import Message from '../../components/Message/Message';
import Loader from '../../components/Loader/Loader';

import { listService1Products } from '../../actions/productServices1Actions'
import Meta from '../../components/Helmet/Meta';

const Utilities_LendScreen = () => {
    const dispatch = useDispatch()

    const productServices1List = useSelector(state => state.productServices1List)
    const { loading, error, productServices1 } = productServices1List

    const [numberOfItems, setNumberOfItems] = useState(3);

    useEffect(() => {
        dispatch(listService1Products())
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [dispatch])

    const showMore = () => {
        if (numberOfItems + 3 <= productServices1.length) {
            setNumberOfItems(numberOfItems + 3)
        } else {
            setNumberOfItems(productServices1.length)
        }
    }

    return (

        <div className="Serv1LendScreen">
            <Meta
                title=" | Utilities servs1"
            />
            <Container>
                <h1 className="p-3" style={{ textAlign: 'center' }}>All servs1</h1>
                {
                    loading
                        ? <Loader />
                        : error ? <Message variant='danger'>{error}</Message>
                            : (
                                <Row>
                                    {
                                        productServices1
                                            .slice(0, numberOfItems)
                                            .map(serv1 => (
                                                <Services1
                                                    key={serv1._id}
                                                    _id={serv1._id}
                                                    name={serv1.name}
                                                    image={serv1.image}
                                                    targetPlant={serv1.target_plant}
                                                    price={serv1.price}
                                                    quantity={serv1.quantity}
                                                />
                                            ))
                                    }
                                    {
                                        numberOfItems >= productServices1.length
                                            ? <Alert style={{ backgroundColor: 'red' }} className="col-md-12 text-center"></Alert>
                                            : ''
                                    }
                                    <Button className="col-md-12 text-center" variant="success outline-dark" onClick={showMore}> </Button>
                                </Row>
                            )
                }
            </Container>
        </div>
    )
}

export default Utilities_LendScreen
